import "core-js/modules/es.array.map.js";

/* eslint-disable */
export default {
  mounted: function mounted() {
    var L = window.L;
    this.map = L.map("map", {
      center: [0, 115],
      zoom: 2,
      zoomControl: false,
      attributionControl: false
    });
    L.tileLayer("https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png", {
      attribution: '&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors',
      maxZoom: 18
    }).addTo(this.map);
  },
  watch: {
    geolocation: {
      handler: function handler(val) {
        if (val) {
          this.map.setView([val.lat, val.lon], 12);
          var icon = L.divIcon({
            className: "w-24 h-24 rounded-full bg-blue-800 text-white text-xs",
            html: '<div class="text-xs">' + "</div>"
          });

          if (this.marker) {
            this.map.removeLayer(this.marker);
          }

          this.marker = L.marker([val.lat, val.lon], {
            icon: icon
          }).addTo(this.map);
        }
      },
      deep: true
    }
  },
  computed: {
    geolocation: function geolocation() {
      return this.$store.state.geolocation;
    }
  },
  data: function data() {
    return {
      map: null,
      marker: null,
      imageIcon: require("../../apps/apps_assets/icon.png")
    };
  }
};